@font-face {
  font-family: "averto";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.ourclient__container {
  display: flex;
  margin: 0 120px 0 120px;
}

.ourclient__title {
  font-family: "averto", sans-serif;
  font-size: 40px;
  font-weight: 400;
  line-height: 49px;
  color: #000;
  margin: 0;
}

.ourclient__descrip {
  font-family: "averto", sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  color: #757575;
  margin-top: 17px;
}

.ourclient-images {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 49px;
}

.ourclient-img {
  margin-left: 37px;
  margin-right: 30px;
}